body{
    font-family: sans-serif;
}

input{
    border-radius: 8px;
    border: none;
    padding: 6px 16px;
    outline: none;
}

button{
    border-radius: 8px;
    border: none;
    padding: 6px 16px;
    margin: 0 5px;
    outline: none;
}

button:hover{
    background-color: darkturquoise;
    color: #ffff;
    cursor: pointer;
}

li{
    margin-right: 8px;
}

.input-area {
  background-color: #c1ffff;
  width: 400px;
  height: 30px;
  padding: 8px;
  margin: 8px;
  border-radius: 8px;
}

.incomplete-area {
  background-color: #c6ffe2;
  width: 400px;
  padding: 8px;
  margin: 8px;
  border-radius: 8px;
}

.complete-area {
  background-color: #ffffe0;
  width: 400px;
  padding: 8px;
  margin: 8px;
  border-radius: 8px;
}

.title{
    text-align: center;
    margin-top: 0;
}

.list-row{
    display: flex;
    align-items: center;
    padding-bottom: 4px;
}